* {
    margin: 0;
    padding: 0;
    font-family: Nunito Sans;
}


.name {
    font-family: Madimi One;
    font-size: 60px;
    line-height: 1.75cm;

    text-align: center;
    // font-weight: 600;
    color: #08a5a8;
}

.buttons {
    margin-top: .5cm;
    display: flex;
    justify-content: center;
    gap: .3cm;
}

.helpline {
    padding: .3cm 1.5cm;
    // width: 100%;
    border-radius: 1000px;
    background-color: #08a5a8;
    border: none;
    color: #e9feff;
    font-size: 16px;
    letter-spacing: .01cm;
    cursor: pointer;
    border: 2.5px solid #08a5a8;
    transition: .3s ease;

}

.hp {
    &:hover {
        background-color: transparent;
    }
}

.donate {
    background-color: transparent;
    // color: black;
    border: 2.5px solid #08a5a8;
    &:hover {
        background-color: #08a5a8;
    }
}

.upper-left {
    width: 12cm;
    display: flex;
    flex-direction: column;
    gap: .1cm;
    // border: thin solid white;
    align-items: center;
}
.upper-right{
    width: 11cm;
    flex-shrink: 0;
}


.yt-video {
    width: 100%;
    aspect-ratio: 16/9;
    border: none;
    border-radius: 10px;
  }
.welcome {
    color: #e9feff;
    font-size: 20px;

}

.home-upper {
    padding: 3cm 10vw;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &::before {
        position: absolute;
        content: "";
        inset: 0;
        background-image: url("back.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        z-index: -1;
        filter: brightness(40%);
    }
}

